.main-gray {
    background-color: #f5f5f5;

    .title {
        font-weight: bold;
        font-size: 1rem;
    }
    .font-btn {
        color: #000 !important;
    }

    .mission-font-color {
        color: #4d4d98;
    }

    .mission-num {
        height: 1rem;
        overflow: hidden;
    }

    .mission-name,
    .mission-id,
    .creator {
        font-weight: bold;
    }

    .common-font {
        font-size: 0.8rem;
    }

    .launches {
        font-weight: 100;
    }

    .year-btn {
        border-radius: 5px;
    }
}

@media screen and (min-width: 320px) and (max-width: 410px) {
    .year-btn {
        padding: 0.5rem 2rem;
        margin: 0.2rem;
    }
}

@media screen and (min-width: 411px) and (max-width: 575px) {
    .year-btn {
        padding: 0.5rem 3rem;
        margin: 0.5rem 1rem;
    }
}

@media screen and (min-width: 576px) and (max-width: 767px) {
    .year-btn {
        padding: 0.5rem 3rem;
        margin: 0.5rem 1rem;
    }
}

@media screen and (min-width: 768px) and (max-width: 991px) {
    .year-btn {
        padding: 0.5rem 1.5rem;
    }

}

@media screen and (min-width: 992px) and (max-width: 1024px) {
    .year-btn {
        padding: 0.5rem 1rem;
        font-size: 0.9rem;
    }

    .grid-view {
        flex: 0 0 50% !important;
        max-width: 50% !important;
    }
}

@media screen and (min-width: 1025px) {
    .year-btn {
        padding: 0.5rem 1rem;
    }
}

@media only screen and (max-width: 1920px) {
    .filter-center {
        justify-content: space-around;
    }
}
